.header__bottom {
    .input[type="search"] {
        height: 35px;
    }
}
.input{
    width: 100%;
    border: none;
    padding: 10px 15px;
    font-size: 13px;
    border-radius: 10px;
    -webkit-appearance: none;
    &-wrap{
        position: relative;
        display: flex;
        .searchBtn{
            position: absolute;
            top: 10px;
            right: 15px;
        }
    }
    &::placeholder{
        color: rgba(0,0,0,.5);
    }
    &[type="search"]{
        padding-right: 40px;
        height: 37px;
        &:focus{
            outline: none;
        }
        &::-webkit-search-cancel-button {
            display: none;
        }
    }
    &:not([type="search"]){
        outline: none;
        transition: all .3s ease-out;
        border: 1px solid var(--Gainsboro);
        &:focus, &:hover{
            border-color: var(--Flamenco)!important;
            transition: all .3s ease-out;
        }
    }
    &-name{
        color: var(--Charcoal);
        font-size: 13px;
        position: absolute;
        top: 12px;
        left: 17px;
        background-color: var(--White);
        transition: all .3s ease-out;
    }
}
.searchBtn{
    color: var(--mainColor);
    font-size: 16px;
    transition: all .3s ease-out;
    &:hover{
        color: var(--Flamenco);
        transition: all .3s ease-out;
    }
}
.input-List{
    margin-bottom: 25px;
    .input-wrap{
        margin-bottom: 15px;
    }
}
.error{
    border-color: var(--Red)!important;
}
.input-radio {
    display: none;
}
.radio {
    display: flex;
    align-items: center;
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}
.input-radio + .radio:before {
    content: "\E037";
    font-family: "icons";
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--White);
    width: 23px;
    height: 23px;
    border: 1px solid var(--Gainsboro);
    border-radius: 50%;
    margin-right: 9px;
    box-sizing: border-box;
    color: var(--White);
    font-size: 9px;
    font-weight: 400;
    cursor: pointer;
    transition: all 0.2s;
}
.input-radio:checked +.radio:before {
    border-color: var(--Wedgewood);
    background-color: var(--Wedgewood);
}

@media(max-width: 600px){
    .input{
        &-name{
            left: 10px;
            font-size: 13px;
            top: 14px;
            &.up{
                top: -6px;
            }
        }
    }
    .input:not([type="search"]):focus + .input-name {
        top: -6px;
        transition: all .3s ease-out;
    }
}